	module.exports = {"data":{"markdownRemark":{"html":"<p>开系全体会议，谈到最近一段大家比较地关心稳定的问题，包括教育系统的决策层。提到要防止一些倾向，把一些一般性的问题上升到原则性的问题，如有的内地来的同学生活比较困难，就说是西部经济发展和沿海地区经济发展的差异所引起的问题。这是一个问题，现在一些分析总是把不具有这种性质的问题上升，找出社会意义和政治意义，这也成为一种\"学问\"。这样的学问在一定的条件下会引起不必要的社会问题。</p>\n<p>做\"学问\"的人往往有一种本能，就是把简单的问题复杂化，把复杂的问题简单化，以为这就是\"学问\"。在一种场合下，这是对的;在另一种条件下，这又是不对的。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0042.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0042.md","frontmatter":{"title":"2月25日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0042.md absPath of file >>> MarkdownRemark"}}

